      width: 100%;
      height: 30px;
      background: #06548F;
      border-radius: 5px;
      text-align: center;
      line-height: 30px;
      color: #fff;
      display: inline-block;
      &:hover, &.active {
        background: @hover-blue;
        color: #fff;
      }
  
    }
  
  }
  
