                        height:14px;
                        background: url(../images/zlm730_06.png);
                        position: relative;
                        top: 3px;
                        left: 10px;
                        cursor: pointer;
                        &:hover{
                            background: url(../images/zlm730_03.png);
                        }
                    }  
                }
				li{
					float: left;
					width: 50%;
